
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html{
  scroll-behavior: smooth;
  scroll-padding-top: 70px;
}

body{
  font-family: "Electrolize", sans-serif; 
}

.container{
  margin-left: 5%;
  margin-right: 5%;
}

.row{
  display:  flex;
  flex-wrap: wrap;
  width: 100%;
  height: 100%;
}
.h-center{
  justify-content: center;
}
.h-right{
  justify-content: flex-end;
}
.v-center{
  align-items: center;
}
.text-center{
  text-align: center;
}
.d-none{
  display: none;
}

.p-5{
  padding: 10vh;
}
.p-3{
  padding: 6vh;
}
.pt-2{
  padding-top: 4vh;
}
.col-md-4, .col-md-5, .col-md-6, .col-md-7, .col-md-10
{
  width: 100%;
}
.mt-1{
  margin-top: 2%;
}

.font-4{
  font-size: 4vh;
}

@media screen and (min-width: 768px){
  .col-md-4{width: 40%;}
  .col-md-5{width: 50%;}
  .col-md-6{width: 60%;}
  .col-md-7{width: 70%;}
  .col-md-10{width: 100%;}

  .de-left-right{
    background-image: linear-gradient(90deg,#65a9f7,#65a9f7,#fff);
    flex-direction: row-reverse;
    margin: 0;
  }
  .de-right-left{
    background-image: linear-gradient(90deg,#fff,#65a9f7,#65a9f7);
    margin: 0;
  }
}

.icon{
  width: 30px;
  height: 30px;
}

.div-icon{
  max-height: 50px;
}

.div-icon:hover span{
  display: block;
}
.div-icon:hover img{
  width: 48px;
  height: 48px;
}

.foto{
  width: 100%;
  border: 2px solid #000;
  border-radius: 0.8rem;
}

.bg-primary{
  background-color: #65a9f7;
}

.center-mouse:hover {
  background-color: #4d4df5;
}


#contato{
  background-color: #29292e;
  color: #fff;
  padding: 20px 0px 20px 0px;
}

#contato div{
  margin: 10px;
}

#contato a{
  text-decoration: none;
  color: #fff;
  font-size: 16pt;
  padding: 20px;
}

#contato a span{
  display: none;
}

#contato a:hover span{
  display: inline-block;
}

#perfil{
  width: 100%;
  border-radius: 50%;
  box-shadow: 0 1rem 1rem #29292e;
  transition: 2s;
}

#perfil:hover{
  border-radius: 5%;
  transition: 2s;
}

#titulo-projeto{
  padding: 2%;
  background-color: #29292e;
  color: #fff;
  margin-bottom: 5px;
  font-size: 6vh;
}

#div-sbm{
  font-size: 4vh;
  margin: 5%;
  background-color: #c3e3f7;
  border: 2px solid #1c1c1f;
  border-radius: 1.5em;
  padding: 4%;
}